.bubble-wrapper {
  position: absolute;
  width: 185px;
  left: 185px;
  top: 120px;
  transform: translateY(-50%);
  z-index: 1000;
  text-align: left;

  .speech-bubble {
    display: inline-block;
    padding: 10px 15px;
    border-radius: .4em;
    background: rgba(0, 0, 0, 0.6);
    color: white;
    word-break: break-word;
    text-align: left;

    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 60%;
      width: 0;
      height: 0;
      border: 0.938em solid transparent;
      border-right-color: rgba(0, 0, 0, 0.6);
      border-left: 0;
      border-bottom: 0;
      margin-top: -0.469em;
      margin-left: -0.937em;
    }
  }

  &.centered {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

  &.no-pointer {
    .speech-bubble:after {
      display: none;
    }
  }

  &.from_me {
    .speech-bubble {
      background: rgba(139, 0, 0, 0.9);

      &:after {
        border-right-color: rgba(139, 0, 0, 0.6);
      }
    }
  }

  &.to_me {
    .speech-bubble {
      background: rgba(0, 139, 0, 0.9);

      &:after {
        border-right-color: rgba(0, 139, 0, 0.6);
      }
    }
  }
}
